import * as React from 'react';
import { Col } from 'reactstrap';

import { Choice } from './../Models/Cards';

import StatsComponent from './StatsComponent';
import IComponentProps from '../Models/IComponentProps';

export default class ChoiceComponent extends React.Component<IComponentProps<Choice>, {}> {
    private renderDescription = (): JSX.Element => (
        <span>{this.props.model.description}</span>
    )
    
    private renderRequirement = (): JSX.Element => (
        <StatsComponent stats={this.props.model.requirement!} backgroundClass="card-choice card-choice-requirement" />
    )
    
    private renderSuccess = (): JSX.Element => (
        <StatsComponent isReward stats={this.props.model.success!} backgroundClass="card-choice card-choice-reward" />
    )
    
    private renderFailure = (): JSX.Element => (
        <StatsComponent isReward stats={this.props.model.failure!} backgroundClass="card-choice card-choice-failure" />
    )
    
    public render = () => (
        <Col className={this.props.className}>
            { this.props.model.description && this.renderDescription() }
            { this.props.model.requirement && this.renderRequirement() }
            { this.props.model.success && this.renderSuccess() }
            { this.props.model.failure && this.renderFailure() }
        </Col>
    )
}
