import * as React from 'react';
import { Col, Container } from 'reactstrap';

import { Card } from '../Models/Cards';

import CharacterCardComponent from "./CharacterCardComponent";
import RealityCardComponent from './RealityCardComponent';
import DreamCardComponent from './DreamCardComponent';
import QuestCardComponent from './QuestCardComponent';
import CardBack from './CardBack';

import MemoryIncrToken from './../Assets/MemoryIncrToken.png';
import MemoryDecrToken from './../Assets/MemoryDecrToken.png';
import ProvidenceToken from './../Assets/ProvidenceToken.png';
import MemoryIncrTokenBack from './../Assets/MemoryIncrTokenBack.png';
import MemoryDecrTokenBack from './../Assets/MemoryDecrTokenBack.png';
import ProvidenceTokenBack from './../Assets/ProvidenceTokenBack.png';
import Rules from './Rules';

interface IPrintAndPlayProps {
    cards: { [type: string]: Card[] },
}

export default class PrintAndPlay extends React.Component<IPrintAndPlayProps> {
    private memomyIncrTokenCount = 6;
    private memoryDecrTokenCount = 6;
    private providenceTokenCount = 42;

    public renderCard = (card: Card) => {
        switch (card.type) {
            case "Character": return <CharacterCardComponent {...card} />;
            case "Reality": return <RealityCardComponent {...card} />;
            case "Dream": return <DreamCardComponent {...card} />;
            case "Quest": return <QuestCardComponent {...card} />;
            default: return <div></div>;
        }
    }    

    public renderCardBack = (card: Card) => {
        switch (card.type) {
            case "Character": return <div className="character-card card-back-character" />;
            case "Reality": return <CardBack backgroundClass="card-back-real" />;
            case "Dream": return <CardBack backgroundClass="card-back-dream" />;
            case "Quest": return <CardBack backgroundClass="card-back-quest" />;
            default: return <div></div>;
        }
    }    

    public renderCardGroup = (cards: Card[], perPage: number, colWidth: number): JSX.Element[] => 
        Array.from(Array(Math.ceil(cards.length / perPage)).keys()).map(i => [
            <div className="print-page">
                { Array.from(Array(perPage).keys()).map(j => 
                    (i * perPage + j < cards.length) && <Col xs={colWidth}>{ this.renderCard(cards[i * perPage + j]) }</Col>) }
            </div>,
            <div className="print-page page-flipped">
                { Array.from(Array(perPage).keys()).map(j => 
                    (i * perPage + j < cards.length) && <Col xs={colWidth}>{ this.renderCardBack(cards[i * perPage + j]) }</Col>) }
            </div>
        ]).flat();

    public renderToken = (img: string): JSX.Element => (
        <img src={img} className="token" />
    )
    
    public renderTokens = (): JSX.Element[] => [
        <div className="print-page">
            { Array.from(Array(this.memomyIncrTokenCount).keys()).map(_ => this.renderToken(MemoryIncrToken)) }
            { Array.from(Array(this.memoryDecrTokenCount).keys()).map(_ => this.renderToken(MemoryDecrToken)) }
            { Array.from(Array(this.providenceTokenCount).keys()).map(_ => this.renderToken(ProvidenceToken)) }
        </div>,
        <div className="print-page page-flipped">
            { Array.from(Array(this.memomyIncrTokenCount).keys()).map(_ => this.renderToken(MemoryIncrTokenBack)) }
            { Array.from(Array(this.memoryDecrTokenCount).keys()).map(_ => this.renderToken(MemoryDecrTokenBack)) }
            { Array.from(Array(this.providenceTokenCount).keys()).map(_ => this.renderToken(ProvidenceTokenBack)) }
        </div>
    ]

    public render = (): JSX.Element => (
        <div className="print-body">
            <div className="print-page rules-page">
                {/* <div className="title">DreamQuest</div> */}
                <Container>
                    <Rules />
                </Container>
            </div>
            { Object.entries(this.props.cards).map(entry => entry[0] === "Character" ? this.renderCardGroup(entry[1], 4, 12) : this.renderCardGroup(entry[1], 9, 4)) }
            { this.renderTokens() }
        </div>
    )
}
