import * as React from 'react';
import { Navbar, Nav, NavLink, Container, NavbarBrand, Row, Col, Button } from 'reactstrap';
import { groupBy } from 'lodash';

import { Card, Cards } from './../Models/Cards';

import CardsJSON from './../Data/Cards.json';
import exportedTypeSuite from './../Models/Cards-ti';
import {createCheckers} from "ts-interface-checker";

import SectionHeader from './SectionHeader';
import AnyCard from './AnyCard';
import Rules from './Rules';
import PrintAndPlay from './PrintAndPlay';

const checker = createCheckers(exportedTypeSuite);
checker.Cards.check(CardsJSON);

interface IAppState {
    cards: { [type: string]: Card[] },
}

export default class App extends React.Component<{}, IAppState> {
    public state: IAppState = {
        cards: groupBy((CardsJSON as Cards).cards, "type"),
    }

    public render = () => [
        <Navbar className="sticky-top" light color="light">
            <NavbarBrand className="card-brand">DreamQuest</NavbarBrand>
            <Nav>
                <NavLink href="#">Home</NavLink>
                <NavLink href="#Rules">Rules</NavLink>
                { Object.keys(this.state.cards).map(title => <NavLink href={`#${title} cards`}>{`${title} cards`}</NavLink>)}
            </Nav>
        </Navbar>,
        <main role="main">
            <Container>
                <Row>
                    <Col className="text-center">
                        <div className="title">DreamQuest</div>
                        <Button color="primary" className="print-and-play-button" onClick={() => window.print()}>{`Download Print & Play kit`}</Button>
                    </Col>
                </Row>
                <Row>
                    <SectionHeader title={"Rules"} />
                    <Rules />
                </Row>
                { Object.entries(this.state.cards).map((pair) => 
                    <Row>
                        <SectionHeader title={`${pair[0]} cards`} />
                        { pair[1].map(card => <AnyCard {...card} />) }
                    </Row>)
                }
            </Container>
        </main>,
        <PrintAndPlay { ...this.state } />
    ]
}
