import CardArt from './../Assets/CardArt.png';
import CharacterArt from './../Assets/CharacterArt.png';

export const getUrlPrefix = (): string => {
    if (location.hostname === "localhost") {
        return "";
    }
    return "https://raw.githubusercontent.com/Haaxor1689/DreamQuest/master";
}

export const getCardArt = (relativePath: string): string => relativePath === "" ? CardArt : getUrlPrefix() + relativePath;
export const getCharacterArt = (relativePath: string): string => relativePath === "" ? CharacterArt : getUrlPrefix() + relativePath;