import * as React from 'react';
import { Row, Col } from 'reactstrap';

import { QuestCard } from '../Models/Cards';

import BaseCardComponent from './BaseCardComponent';
import DividerComponent from './DividerComponent';
import ChoiceComponent from './ChoiceComponent';
import StatsComponent from './StatsComponent';

export default class QuestCardComponent extends React.Component<QuestCard, {}> {
    private renderBody = (): JSX.Element[] => [
        <DividerComponent/>,
        <Row>
            { this.props.conditions.map((choice) => <StatsComponent stats={choice} backgroundClass="quest-condition" />) }
        </Row>
    ]
    
    public render = () => <BaseCardComponent card={this.props} backgroundClass="card-body-dream" body={this.renderBody()} />
}
